<template>
  <div class="hs-container">
    <c-header :title="$t('base.Setting')" isBackNav fixedBackPath="/personal/center"></c-header>
    <hs-scroll
      ref="tableView"
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_pullingDown">
      <ul>
        <hs-section>
          <hs-cell-1 @click.native="$router.push('/personal/linkCellPhone')">
            <div class="custom-cell-link-phone">
              <p>{{$t('base.LinkYourCellphone')}}</p>
              <p>{{details.UserInfo && details.UserInfo.Mobile ? details.UserInfo.Mobile : $t('base.Unlinked')}}</p>
            </div>
          </hs-cell-1>
          <!-- <hs-cell-1 :value="$t('base.PersonalInformation')" @click.native="$router.push('/personal/information')"></hs-cell-1> 暂不开放个人信息 暂不开放个人信息 暂不开放个人信息-->
          <hs-cell-1 :value="$t('base.ChangePassword')" @click.native="$router.push('/personal/resetPassword')"></hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-language">
              <p>{{$t('base.Language')}}</p>
              <div class="switch" @click="_toggleLanguage">
                <p :class="{actived: language === 'CN'}">{{$t('base.CN')}}</p>
                <p :class="{actived: language === 'EN'}">{{$t('base.EN')}}</p>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <div class="footer">
          <hs-gradient-button style="margin-top: 30px" :title="$t('base.SignOut')" @click="_initiateExit"></hs-gradient-button>
          <p class="tips">{{$t('base.CurrentVersion')}}: v{{version}}</p>
        </div>
      </ul>
    </hs-scroll>
  </div>
</template>
<script type="text/ecmascript-6">
// 设置
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import {mapMutations, mapActions} from 'vuex'
import {kpiUserLogout, kpiUserGetUserCenterInitInfo} from 'api'
export default {
  created() {
    this._getData()
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {}
    }
  },
  computed: {
    version() {
      return window.localStorage.version
    }
  },
  methods: {
    ...mapMutations([
      'SET_UN_HANDLE_VETTING_COUNT',
      'SET_PERSONAL_CENTER_DATA'
    ]),
    ...mapActions([
      'dispatchPersonalCenterData'
    ]),
    /** 发起退出 */
    _initiateExit() {
      this.$alert({
        content: this.$t('TM.MakeSureToLeaveYourCurrentAccount'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          this._exit()
        }
      })
    },
    /** 退出 */
    _exit(isConfirm = false) {
      let params = {
        // eslint-disable-next-line
        isConfirm: isConfirm === true ? true : false
      }
      kpiUserLogout(params).then((res) => {
        console.log('退出: ', res)
        if (res.ResultData === true) {
          this._exitSuccess()
        } else {
          this.$alert({
            content: this.$t('TM.ExitWarning'),
            cancelTxt: this.$t('base.Cancel'),
            confirmFn: () => {
              this._exit(true)
            }
          })
        }
      })
    },
    /** 退出成功 */
    _exitSuccess() {
      this.SET_UN_HANDLE_VETTING_COUNT(0)
      this.SET_PERSONAL_CENTER_DATA({})
      this.$router.push('/login')
    },
    _getData() {
      this.dispatchPersonalCenterData().then((res) => {
        console.log('个人中心: ', res)
        this.details = res
      })
    },
    _toggleLanguage() {
      let res = window.localStorage.language
      window.localStorage.language = res === 'CN' ? 'EN' : 'CN'
      window.location.reload()
    },
    _pullingDown() {
      kpiUserGetUserCenterInitInfo({}).then((res) => {
        console.log('强制刷新个人中心: ', res)
        this.details = res.ResultData
        this.SET_PERSONAL_CENTER_DATA(res.ResultData)
      }).catch(() => {
        this.$refs.tableView.forceUpdate()
      })
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1,
    HsGradientButton
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .footer
      @extend .flex-column
      @extend .flex-start
      width 100%
      >.tips
        font-size 0.8rem
        color $color_gray_text
        line-height 1rem
        margin-top 10px
    .custom-cell-link-phone
      @extend .flex-row
      @extend .flex-between
      width 100%
      >p
        &:nth-child(2)
          color $color_gray_text
    .custom-cell-language
      @extend .flex-row
      @extend .flex-between
      width 100%
      .switch
        @extend .flex-row
        @extend .flex-between
        width 80px
        height 26px
        border-radius 4px
        background-color $color_gray_layer
        >p
          @extend .flex-center
          height 100%
          flex 1
          border-radius 4px
          background-color $color_gray_layer
          color $color_white
          font-size 0.9rem
        >.actived
          background-color $color_theme
</style>



